"use client";

import { useState, useEffect } from "react";
import SearchBar from "./SearchBar";
import EditJuryForm from "./EditJuryForm";
import DeleteJuryButton from "./DeleteJuryButton";

export default function JuryList() {
  const [jurys, setJurys] = useState<{ idJury: number; nom: string }[]>([]); // Correcting type to match your data structure
  const [searchTerm, setSearchTerm] = useState("");
  const [editingJury, setEditingJury] = useState<string | null>(null);

  // Fetch jury data from the database
  useEffect(() => {
    const fetchJurys = async () => {
      try {
        const response = await fetch("/api/jurys"); // Fetch from your backend
        const data = await response.json();
        setJurys(data); // Set the jurys state with the fetched data
      } catch (error) {
        console.error("Error fetching juries:", error);
      }
    };

    fetchJurys();
  }, []); // Runs only once when the component mounts

  const handleSearch = (term: string) => {
    setSearchTerm(term);
  };

  const filteredJurys = jurys.filter((jury) =>
    jury.nom.toLowerCase().includes(searchTerm.toLowerCase()) // Filter by jury name
  );

  const handleEdit = (jury: string) => {
    setEditingJury(jury);
  };

  const handleSaveEdit = (updatedJury: string) => {
    setJurys(jurys.map((j) => (j.nom === editingJury ? { ...j, nom: updatedJury } : j)));
    setEditingJury(null);
  };

  const handleDelete = (juryId: number) => {
    setJurys(jurys.filter((jury) => jury.idJury !== juryId));
  };

  return (
    <div className="mt-8">
      <SearchBar onSearch={handleSearch} />
      <ul className="mt-4 space-y-3">
        {filteredJurys.map((jury) => (
          <li key={jury.idJury} className="flex items-center justify-between p-4 bg-gray-50 rounded-lg shadow-sm">
            {editingJury === jury.nom ? (
              <EditJuryForm
                initialName={jury.nom}
                onSave={handleSaveEdit}
                onCancel={() => setEditingJury(null)}
              />
            ) : (
              <>
                <span className="text-gray-700">{jury.nom}</span>
                <div className="flex items-center gap-4">
                  <button
                    onClick={() => handleEdit(jury.nom)}
                    className="text-blue-500 hover:text-blue-700"
                  >
                    Modifier
                  </button>
                  <DeleteJuryButton onDelete={() => handleDelete(jury.idJury)} />
                </div>
              </>
            )}
          </li>
        ))}
      </ul>
    </div>
  );
}
